














































































.ant-result {
  padding: 48px 32px;
  font-family: 'Roboto';
}
.ant-result-icon.ant-result-image, 
.ant-result-title,
.ant-result-subtitle,
.ant-result-extra
 {
    text-align: center;
    padding-top: 38px;
}
.ant-result-title {
    font-size: 35px;
    font-weight: bold;
    color: #fbb93e;
}
.ant-result-subtitle {
    padding: 0;
}
.ant-result-extra {
    padding: 25px;
}
.ant-result-extra button.ant-btn.ant-btn-primary {
    height: 40px;
    border-radius: 5px;
}
