






















































#message-container {
  height: 70vh;
  overflow: auto;
}
.message {
  width: 100%;
  display: block;
  overflow: auto;
  margin-bottom: 10px;
}
.message .v-size--default {
  height: 48px;
}
.receive-message {
  display: block;
  float: left;
}
.message .v-chip__content {
  text-align: left;
}
.border-left {
  border-left: 1px solid;
  border-width: 4px!important;
}
.active {
  background-color: #666666 !important;
}
